import { useEffect, useState } from "react";

//dependencies
import WebinarsList from "../components/videos/webinars";
import VideoLoadingComponent from "../components/videos/videoLoading";
import * as VideoServer from "../services/videoServer";

//components

/**
 * Carga todos los videos que sean Casos para el modulo Casos de la aplicación
 */
function Webinars() {
  const VideoLoading = VideoLoadingComponent(WebinarsList);

  const [appState, setAppState] = useState({
    loading: true,
    videos: null,
  });

  useEffect(() => {
    VideoServer.ListWebinars().then((res) => {
        const allVideos = res.videos;
        setAppState({ loading: false, videos: allVideos });
    });
  }, [setAppState]);

  return (
      <div className="App">
        <VideoLoading isLoading={appState.loading} webinars={appState.videos} />
      </div>
  );
}
export default Webinars;
